import { NextRequest, NextResponse } from 'next/server';

export const runtime = 'edge';

const corsHeaders = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
  'Access-Control-Allow-Headers': 'Content-Type, Authorization',
};

const NAVER_CLIENT_ID = process.env.NAVER_CLIENT_ID;
const NAVER_CLIENT_SECRET = process.env.NAVER_CLIENT_SECRET;

interface NaverLocalItem {
  title: string;
  link: string;
  category: string;
  description: string;
  telephone: string;
  address: string;
  roadAddress: string;
  mapx: string;
  mapy: string;
}

interface NaverNewsItem {
  title: string;
  originallink: string;
  link: string;
  description: string;
  pubDate: string;
}

export async function OPTIONS() {
  return new NextResponse(null, { status: 200, headers: corsHeaders });
}

// GET: ÌäπÏ†ï ÏúÑÏπò Ï£ºÎ≥Ä Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url);
    const type = searchParams.get('type') || 'competitors'; // competitors, apartments, news
    const query = searchParams.get('query') || 'ÎåÄÏπòÎèô ÌïôÏõê';
    const display = parseInt(searchParams.get('display') || '10');

    if (!NAVER_CLIENT_ID || !NAVER_CLIENT_SECRET) {
      return NextResponse.json({
        success: false,
        error: 'Naver API credentials not configured',
        demo_mode: true,
        data: getDemoData(type)
      }, { status: 200, headers: corsHeaders });
    }

    let result;
    
    if (type === 'news') {
      result = await searchNews(query, display);
    } else {
      result = await searchLocal(query, display);
    }

    return NextResponse.json({
      success: true,
      type,
      query,
      data: result
    }, { status: 200, headers: corsHeaders });

  } catch (error: any) {
    console.error('Geo API Error:', error);
    return NextResponse.json(
      { success: false, error: error.message },
      { status: 500, headers: corsHeaders }
    );
  }
}

// POST: Ï†ÑÎûµ Î∂ÑÏÑù ÏöîÏ≤≠
export async function POST(request: NextRequest) {
  try {
    const body = await request.json();
    const { center_lat, center_lng, radius_km = 2, academy_name } = body;

    if (!NAVER_CLIENT_ID || !NAVER_CLIENT_SECRET) {
      return NextResponse.json({
        success: true,
        demo_mode: true,
        data: getStrategicAnalysis(academy_name || 'Ïö∞Î¶¨ÌïôÏõê')
      }, { status: 200, headers: corsHeaders });
    }

    // 1. Í≤ΩÏüÅÏÇ¨ Í≤ÄÏÉâ
    const competitors = await searchLocal(`${academy_name || 'ÎåÄÏπòÎèô'} ÌïôÏõê`, 20);
    
    // 2. ÏïÑÌååÌä∏ Îã®ÏßÄ Í≤ÄÏÉâ
    const apartments = await searchLocal(`${academy_name || 'ÎåÄÏπòÎèô'} ÏïÑÌååÌä∏`, 20);
    
    // 3. ÍµêÏú° Îâ¥Ïä§ Í≤ÄÏÉâ
    const news = await searchNews('ÍµêÏú° Ï†ïÏ±Ö ÏûÖÏãú', 10);

    // 4. Ï†ÑÎûµ Î∂ÑÏÑù
    const analysis = analyzeStrategicData(competitors, apartments, news);

    return NextResponse.json({
      success: true,
      data: {
        center: { lat: center_lat, lng: center_lng },
        radius_km,
        competitors: competitors.slice(0, 10),
        customer_zones: apartments.slice(0, 10),
        news: news.slice(0, 5),
        analysis
      }
    }, { status: 200, headers: corsHeaders });

  } catch (error: any) {
    console.error('Geo API Error:', error);
    return NextResponse.json(
      { success: false, error: error.message },
      { status: 500, headers: corsHeaders }
    );
  }
}

// ÎÑ§Ïù¥Î≤Ñ ÏßÄÏó≠ Í≤ÄÏÉâ
async function searchLocal(query: string, display: number): Promise<any[]> {
  const encodedQuery = encodeURIComponent(query);
  const url = `https://openapi.naver.com/v1/search/local.json?query=${encodedQuery}&display=${display}`;
  
  const response = await fetch(url, {
    headers: {
      'X-Naver-Client-Id': NAVER_CLIENT_ID!,
      'X-Naver-Client-Secret': NAVER_CLIENT_SECRET!,
    },
  });

  const data = await response.json();
  
  if (data.errorCode) {
    throw new Error(data.errorMessage);
  }

  return (data.items || []).map((item: NaverLocalItem) => ({
    name: item.title.replace(/<[^>]*>/g, ''), // HTML ÌÉúÍ∑∏ Ï†úÍ±∞
    category: item.category,
    address: item.roadAddress || item.address,
    lat: parseFloat(item.mapy) / 10000000,
    lng: parseFloat(item.mapx) / 10000000,
    link: item.link,
    telephone: item.telephone,
    type: categorizePlace(item.category)
  }));
}

// ÎÑ§Ïù¥Î≤Ñ Îâ¥Ïä§ Í≤ÄÏÉâ
async function searchNews(query: string, display: number): Promise<any[]> {
  const encodedQuery = encodeURIComponent(query);
  const url = `https://openapi.naver.com/v1/search/news.json?query=${encodedQuery}&display=${display}&sort=date`;
  
  const response = await fetch(url, {
    headers: {
      'X-Naver-Client-Id': NAVER_CLIENT_ID!,
      'X-Naver-Client-Secret': NAVER_CLIENT_SECRET!,
    },
  });

  const data = await response.json();
  
  if (data.errorCode) {
    throw new Error(data.errorMessage);
  }

  return (data.items || []).map((item: NaverNewsItem) => ({
    title: item.title.replace(/<[^>]*>/g, ''),
    description: item.description.replace(/<[^>]*>/g, ''),
    link: item.originallink || item.link,
    pubDate: item.pubDate,
    category: categorizeNews(item.title + item.description)
  }));
}

// Ïû•ÏÜå Ïú†Ìòï Î∂ÑÎ•ò
function categorizePlace(category: string): string {
  if (category.includes('ÌïôÏõê') || category.includes('ÍµêÏú°')) return 'competitor';
  if (category.includes('ÏïÑÌååÌä∏') || category.includes('Ï£ºÍ±∞')) return 'customer_zone';
  if (category.includes('ÌïôÍµê')) return 'school';
  return 'other';
}

// Îâ¥Ïä§ Ïπ¥ÌÖåÍ≥†Î¶¨ Î∂ÑÎ•ò
function categorizeNews(text: string): string {
  if (text.includes('ÏûÖÏãú') || text.includes('ÏàòÎä•') || text.includes('ÎåÄÌïô')) return 'entrance_exam';
  if (text.includes('Ï†ïÏ±Ö') || text.includes('ÍµêÏú°Î∂Ä') || text.includes('ÍµêÏú°Ï≤≠')) return 'policy';
  if (text.includes('ÌïôÏõê') || text.includes('ÏÇ¨ÍµêÏú°')) return 'private_education';
  return 'general';
}

// Ï†ÑÎûµ Î∂ÑÏÑù
function analyzeStrategicData(competitors: any[], apartments: any[], news: any[]) {
  const competitorCount = competitors.length;
  const avgCompetitorDensity = competitorCount / 4; // 2km Î∞òÍ≤Ω Í∏∞Ï§Ä
  
  const threatLevel = competitorCount > 15 ? 'high' : competitorCount > 8 ? 'medium' : 'low';
  const opportunityScore = apartments.length * 0.1;
  
  const policyNews = news.filter(n => n.category === 'policy');
  const entranceNews = news.filter(n => n.category === 'entrance_exam');

  return {
    threat_level: threatLevel,
    competitor_count: competitorCount,
    opportunity_score: Math.min(1, opportunityScore).toFixed(2),
    customer_zone_count: apartments.length,
    recent_policy_changes: policyNews.length,
    entrance_exam_updates: entranceNews.length,
    recommendations: generateRecommendations(threatLevel, apartments.length, policyNews.length)
  };
}

